.main-nav {
  position: relative;
  width: 100%; }

.main-nav__toggle {
  display: none; }

.main-nav__site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 40px;
  margin-left: auto;
  width: 600px;
  font-weight: 500;
  line-height: 20px; }

.main-nav__site-item a {
  display: block;
  width: auto;
  color: #F9FBFD; }

.main-nav__site-item a[href]:hover {
  color: #808080; }

.main-nav__site-item a[href]:active {
  opacity: 0.5; }

@media (max-width: 1023px) {
  .main-nav__site-list {
    margin-top: 25px;
    width: 550px;
    font-size: 14px;
    line-height: 17px; } }

@media (max-width: 767px) {
  .main-nav__site-list {
    width: 100%;
    flex-direction: column;
    margin: 0 auto;
    font-size: 18px;
    line-height: 22px; }
  .main-nav__site-item a {
    padding-bottom: 30px;
    padding-left: 15px;
    color: #011C40; }
  .main-nav__toggle {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 22px;
    height: 20px;
    z-index: 10;
    background-color: transparent;
    border: 0;
    outline: none;
    cursor: pointer; }
    .main-nav__toggle:hover {
      opacity: 0.5; }
    .main-nav__toggle:active {
      opacity: 0.2; }
  .main-nav--closed .main-nav__toggle {
    top: -48px;
    left: 280px; }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 1px;
    background-color: #F9FBFD;
    box-shadow: 0 9px 0 0 #F9FBFD, 0 18px 0 0 #F9FBFD; }
  .main-nav--open .main-nav__toggle {
    top: -45px;
    left: 280px; }
    .main-nav--open .main-nav__toggle::before, .main-nav--open .main-nav__toggle::after {
      content: "";
      position: absolute;
      top: 5px;
      right: 5px;
      width: 20px;
      height: 2px;
      background-color: #011C40; }
    .main-nav--open .main-nav__toggle::before {
      transform: rotate(45deg); }
    .main-nav--open .main-nav__toggle::after {
      transform: rotate(-45deg); }
  .main-nav--nojs .main-nav__toggle {
    display: none; }
  .main-nav--nojs .main-nav__site-list {
    display: none; }
  .main-nav--closed .main-nav__site-list {
    display: none; }
  .main-nav--open {
    margin-bottom: 70px; } }

